import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material';
import { Router } from '@angular/router';
import { PersonnePhysiqueComponent } from '@app/components/personnePhysique/personne-physique.component';
import { CommonService, SearchObjectTreatment, TAdmPersonnel } from '@app/shared';
import { AdmPersonnelService } from '@app/shared/services/administration/adm-personnel.service';
import { AdmProfileService } from '@app/shared/services/administration/admProfile.service';
import { ConfigService } from '@app/shared/services/config.service';
import { LayoutService } from '@app/shared/services/layout.service';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-fiche-personel',
  templateUrl: './fiche-personel.component.html',
  styleUrls: ['./fiche-personel.component.scss']
})
export class FichePersonelComponent implements OnInit {
  formAddPersonnel: FormGroup;
  personnel: TAdmPersonnel;
  personnePhysique: any;
  currentLang: string;
  idPersonne: number;
  edit: boolean = false;
  affpersonnephy: boolean = false;
  subscription: any;
  dtIntegration: any;
  dtFinIntegration: any;
  dtDiplome: any;
  @ViewChild('refPersonnePhysique', { static: false }) refPersonnePhysique: PersonnePhysiqueComponent;
  idPersonneSauvegarde: any;
  constructor(public personelService: AdmPersonnelService,
    private toastr: ToastrService,
    private searchObjectTreatment: SearchObjectTreatment,
    private dialog: MatDialog,
    private formBuilder: FormBuilder,
    private translate: TranslateService,
    private layoutService: LayoutService,
    private configService: ConfigService,
    private router: Router,
    private commonService: CommonService) {

    this.currentLang = this.translate.getDefaultLang();

    this.translate.onDefaultLangChange.subscribe((language) => {
      console.log(this.translate.getDefaultLang() + ' vs ' + language.lang);
      this.currentLang = language.lang;
    });

  }

  ngOnInit() {
    this.affpersonnephy = false ;
    if (this.personelService.edit == true) {
      debugger
      this.personnel = new TAdmPersonnel();
      this.personelService.getPersonel(+ sessionStorage.getItem('idPersonnel')).subscribe(
        response => {
          debugger
          if (response['code'] === '200') {
            this.personnel = response['payload'];
            console.log("nchala te5dem" + this.idPersonne);
          //  this.affpersonnephy = true;
            this.initAddForm();
            this.formAddPersonnel.get('flgActif').setValue(this.personnel.flgActif);
            this.formAddPersonnel.get('dtIntegration').setValue(this.personnel.dtIntegration);
            this.formAddPersonnel.get('dtFinIntegration').setValue(this.personnel.dtFinIntegration);
            this.formAddPersonnel.get('dtDiplome').setValue(this.personnel.dtDiplome);
            console.log("nchala te5dem" + this.personnel.idPersonnePhysique);
            debugger
            this.personelService.getPersonnePhysiqueByIdPersonnePhysique(this.personnel.idPersonnePhysique).subscribe(
              response => {
    debugger
                if (response['code'] === '200') {
                  debugger
                  this.personnePhysique = response['payload'];
                  this.idPersonne = this.personnePhysique.id;
                  this.affpersonnephy = true ;
                }
              },
    
              error => {
                console.log('error :: ' + error);
              });
    

          }
        },
        error => {
          console.log('error :: ' + error);
        });

    




    }
    else {
      this.initAddForm();
      this.personnel = new TAdmPersonnel();
      this.affpersonnephy = true;
    }


  }



  savePersonnel() {
    debugger
    console.log(this.formAddPersonnel);
    if (this.formAddPersonnel.valid) {

      if (this.personelService.edit == true) {
        debugger

        this.refPersonnePhysique.validateFormEdit();

        this.personnel.idPersonnePhysique = this.idPersonne;
        this.personnel.matricule = this.formAddPersonnel.get('matricule').value;
        this.personnel.mail = this.formAddPersonnel.get('mail').value;
        this.personnel.flgActif = this.formAddPersonnel.get('flgActif').value;
        this.dtIntegration = this.commonService.convertDateToTimeStamp(this.formAddPersonnel.get('dtFinIntegration').value);
        this.dtFinIntegration = this.commonService.convertDateToTimeStamp(this.formAddPersonnel.get('dtFinIntegration').value);
        this.dtDiplome = this.commonService.convertDateToTimeStamp(this.formAddPersonnel.get('dtDiplome').value);
        this.personnel.dtIntegration = this.dtIntegration;
        this.personnel.dtFinIntegration = this.dtFinIntegration;
        this.personnel.dtDiplome = this.dtDiplome;
        if (this.personnel.dtAjout != null)
          this.personnel.dtAjout = this.commonService.convertDateToTimeStamp(this.personnel.dtAjout);
        if (this.personnel.dtMaj != null)
          this.personnel.dtMaj = this.commonService.convertDateToTimeStamp(this.personnel.dtMaj);
        console.log("hamaaaaaaaaa" + this.personnel);
        this.personelService.updatePersonnel(this.personnel).subscribe(
          response => {
            if (response['code'] === '200') {
              if (this.currentLang == 'ar') {
                this.toastr.success('', ' عملية ناجحة');
              } else {
                this.toastr.success('', 'opération effectuée avec succés!');
              }
              this.router.navigateByUrl('/pers/personnels');
            }

          },
          error => {
            console.log('error :: ' + error);
          });







      } else {
        debugger
        this.refPersonnePhysique.validateForm();
        this.refPersonnePhysique.getIdPersonneResult().subscribe(
          (response) => {
            debugger

            if (response != null) {
              this.idPersonneSauvegarde = response;

              let admPersonnel = new TAdmPersonnel;
              console.log(this.idPersonneSauvegarde);
              if (this.idPersonneSauvegarde != null)
                this.getIdPersonnePhysique();

              else {
                //operation echoué    
                return;
              }


            } else {
              //operation echoué    
              return;
            }



          }, (err) => {
            // partie erreur alert(err)
          });

      }
    } else {
      alert("invalid form")
    }

  }

  initAddForm() {




    // const dtPiece = this.personnel['dtIntegration'] === undefined ? null : this.personnel['dtIntegration'].replace(" ", "T")+"Z";
    this.formAddPersonnel = this.formBuilder.group({
      matricule: [null, [Validators.required]],
      mail: [null, [Validators.required]],
      flgActif: ['1', [Validators.required]],
      dtIntegration: [null, [Validators.required]],
      dtFinIntegration: [null, [Validators.required]],
      dtDiplome: [null, [Validators.required]],

    })
  }

  onCancel() {

    this.router.navigateByUrl('/pers/personnels');
  }


  getPersonel() {
    this.personnel = new TAdmPersonnel();
    this.personelService.getPersonel(+ sessionStorage.getItem('idPersonnel')).subscribe(
      response => {

        if (response['code'] === '200') {
          this.personnel = response['payload'];
          if (this.personnel != null)
            this.edit = true;
          else
            this.edit = false;

          if (this.edit = true) {
            this.formAddPersonnel = this.formBuilder.group({
              matricule: [null, [Validators.required]],
              mail: [null, [Validators.required]],
              flgActif: [this.personnel.flgActif, [Validators.required]],
              dtIntegration: [null, [Validators.required]],
              dtFinIntegration: [null, [Validators.required]],
              dtDiplome: [null, [Validators.required]],

            })
            this.idPersonne = this.personnel.idPersonnePhysique;
          }
          else {
            this.initAddForm();
            this.personnel = new TAdmPersonnel();
          }
        }

      },
      error => {
        console.log('error :: ' + error);
      });

  }


  getIdPersonnePhysique() {
    debugger
    let admPersonnel = new TAdmPersonnel;
    this.personelService.getPersonnePhysique(this.idPersonneSauvegarde).subscribe(
      response => {

        if (response['code'] === '200') {

          this.personnePhysique = response['payload'];
          this.idPersonne = this.personnePhysique.id;

          admPersonnel.idPersonnePhysique = this.personnePhysique.id; //this.formAddPersonnel.get('idPersonnePhysique').value;
          this.idPersonne = admPersonnel.idPersonnePhysique;
          admPersonnel.matricule = this.formAddPersonnel.get('matricule').value;
          admPersonnel.mail = this.formAddPersonnel.get('mail').value;
          admPersonnel.flgActif = this.formAddPersonnel.get('flgActif').value;
          admPersonnel.dtIntegration = new Date(this.formAddPersonnel.get('dtIntegration').value);
          admPersonnel.dtFinIntegration = new Date(this.formAddPersonnel.get('dtFinIntegration').value);
          admPersonnel.dtDiplome = new Date(this.formAddPersonnel.get('dtDiplome').value);
          this.personelService.savePersonnel(admPersonnel).subscribe(response => {
            if (response['code'] === '200') {
              this.toastr.success('', this.translate.instant('dossiers.successMsg'));
              this.router.navigateByUrl('/pers/personnels');
            }
          }, err => {
            console.log(err);
          })

        }
      },
      error => {
        console.log('error :: ' + error);
      });

  }


}
