import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { CONSTANTE_NOMENCLATURE_URL } from '@app/shared/constante';
import { CONSTANTE_PERSONNE } from '@app/shared/constante/constante_pp_pm';
import { TAdmPersonnel } from '@app/shared/entites';
import { Observable, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AdmPersonnelService {
  public idPersonnel: number;
  public edit: boolean;

  private perso = new Subject<TAdmPersonnel>();
  constructor(private http: HttpClient) { }

  public getListPersonnel(searchObject: Object): Observable<Object> {


    return this.http.post("http://localhost:8060/administration/personnel/data", searchObject);


  }

  public savePersonnel(personnel: TAdmPersonnel) {

    return this.http.post("http://localhost:8060/administration/personnel/", personnel);
  }

  public getPersonel(idpersonnel: number) {

    return this.http.get("http://localhost:8060/administration/personnel/" + idpersonnel);
  }

  public getPersonnePhysique(id: number) {

    return this.http.get("http://localhost:8060/app/personnePhysique/" + id);
  }

  public updatePersonnel(pers: TAdmPersonnel) {

    return this.http.put(`${CONSTANTE_NOMENCLATURE_URL.UPDATE_PERSONNELLE}`, pers);

  }


  sendPerso(personnel: TAdmPersonnel) {
    this.perso.next(personnel);
  }

  clearPerso() {
    this.perso.next();
  }

  getPerso(): Observable<TAdmPersonnel> {
    return this.perso.asObservable();
  }

  public getPersonnePhysiqueByIdPersonnePhysique(id: number) {

    return this.http.get(CONSTANTE_PERSONNE.URL_PP_FIND_BY_ID + id);
  }


   
  public deletePersonnelById(idObject: Number)
  {  
    return this.http.delete(CONSTANTE_NOMENCLATURE_URL.DELETE_PERSONNEL+ idObject);
  }
 
}
