import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialog, MatTableDataSource } from '@angular/material';
import { Router } from '@angular/router';
import { CommonService, CONSTANTE_SQL, CriteriaSearch, SearchObject, SearchObjectTreatment, TAdmPersonnel } from '@app/shared';
import { ConfigService } from '@app/shared/services/config.service';
import { LayoutService } from '@app/shared/services/layout.service';
import { TranslateService } from '@ngx-translate/core';
 
import { VPersonnel } from '@app/shared/entites/administration/VPersonnel';
import { AdmPersonnelService } from '@app/shared/services/administration/adm-personnel.service';
 

@Component({
  selector: 'app-personnel',
  templateUrl: './personnel.component.html',
  styleUrls: ['./personnel.component.scss']
})
export class PersonnelComponent implements OnInit {
  currentLang: any;
  searchObject: SearchObject;
  formPersonnelSearch: FormGroup;
  public config: any = {};
  displayedColumns: string[] = ['matricule', 'name','dateAffectation','action'];
  datasource: any;
  rows = [];
  temp = [];
  countPage: Number;
  idPersonne: any;
  formpersonelSearch: FormGroup;
  constructor(
    private personelService : AdmPersonnelService,
    private searchObjectTreatment: SearchObjectTreatment,
    private dialog: MatDialog,
    private formBuilder: FormBuilder,
    private translate: TranslateService,
    private layoutService: LayoutService,
    private commonService: CommonService,
    private configService: ConfigService, private router: Router) { 

    
    this.translate.onDefaultLangChange.subscribe((language) => {
      console.log(this.translate.getDefaultLang() + ' vs' + language.lang);
      this.currentLang = language.lang;
    });
  }

  ngOnInit() {

 
    this.config = this.configService.templateConf;
    this.currentLang = this.config.layout.dir === 'rtl' ? 'ar' : 'fr';
    console.log("curent languague list profil on NgIn",this.currentLang);
    this.searchObject = this.searchObjectTreatment.treatementSearchObject(10, 0, null, null);
    this.initDatatable();

    this.formPersonnelSearch = this.formBuilder.group({
      matricule: [null],
      name: [null],
     // juridction: [null],
      dateAffectation: [null],

    }) 
  }

  initDatatable() {
  
    this.personelService.getListPersonnel(this.searchObject).subscribe(
      response => {

        if (response['code'] === '200') {
          this.countPage = response['payload']['total'];
          this.rows = response['payload']['data'];



          this.datasource = new MatTableDataSource<VPersonnel[]>(
            response['payload']['data']
          );

          console.log(this.datasource);
          this.temp = this.rows;
        }
      },
      error => {
        console.log('error :: ' + error);
      }
    )
  }

  redirectFichePersonel() {
    this.personelService.edit=false;
    this.router.navigateByUrl('/pers/fichePersonel');
  }


  editPersonel(element : TAdmPersonnel) {

 this.personelService.sendPerso(element);
    this.personelService.idPersonnel = element.id;
    this.personelService.edit=true;
    this.idPersonne=element.idPersonnePhysique;
    sessionStorage.setItem('idPersonnel' ,( this.personelService.idPersonnel).toString() )
    this.router.navigateByUrl('/pers/fichePersonel');
  }



  onDelete(element) {
    console.log("hama")
    this.personelService.deletePersonnelById(element.id)
      .subscribe(data => {
        console.log("deletePersonnelById")
        this.initDatatable();

      }, err => {
        console.log(err);
      })
      ;

     
  }

  public onSearch(): void {
    this.searchObject.pagination.offSet = 0;
    this.searchObject.pagination.limit = 10;
    this.searchObject.dataSearch = [];

    if (this.formPersonnelSearch.get('matricule').value !== undefined && this.formPersonnelSearch.get('matricule').value !== '' && this.formPersonnelSearch.get('matricule').value !== null)
        this.searchObject.dataSearch.push(
            new CriteriaSearch(
                'matricule',
                (this.formPersonnelSearch.get('matricule').value).toString(),
                CONSTANTE_SQL.UPPER_LIKE
            )
        );

    if (this.formPersonnelSearch.get('name').value !== undefined && this.formPersonnelSearch.get('name').value !== '' && this.formPersonnelSearch.get('name').value !== null)
        this.searchObject.dataSearch.push(
            new CriteriaSearch(
                'name',
                (this.formPersonnelSearch.get('name').value).toString(),
                CONSTANTE_SQL.UPPER_LIKE
            )
        );

    if (this.formPersonnelSearch.get('dateAffectation').value !== undefined && this.formPersonnelSearch.get('dateAffectation').value !== '' && this.formPersonnelSearch.get('dateAffectation').value !== null)
        this.searchObject.dataSearch.push(
            new CriteriaSearch(
                'dateAffectation',
                this.commonService.convertDateToTimeStamp(this.formPersonnelSearch.get('dateAffectation').value),
                CONSTANTE_SQL.EQUALS
            )
        );

  



    this.initDatatable();
}

public onReset() {
  this.searchObject = this.searchObjectTreatment.treatementSearchObject(10, 0, 'dtMaj', 'desc nulls last');
  this.searchObject.dataSearch = [];
  this.initSearch();
  this.initDatatable();
}

public initSearch() {
  this.initFormSearch();
}

public initFormSearch() {
  this.formPersonnelSearch = this.formBuilder.group({
      matricule: [null],
      name: [null],
      dateAffectation: [null]
      
  });
}


}
